import React , {useContext} from 'react'
import "./CheckoutItem.scss"
// import "./new.scss"
import Pepsi from "../Images/2.png"
import {Context} from "../../Context"

export default function CheckoutItem({cartItem}) {
  const {total,reload} = useContext(Context)
  const cart = JSON.parse(localStorage.getItem("cart"))
  const { Name, imageUrl, Price, quantity } = cartItem;

  
  const removeItem = ()=>{
  const filteredCart = cart.filter(content=>content.id !== cartItem.id)
  localStorage.setItem("cart",JSON.stringify(filteredCart))
  reload.set(prv=>[...prv]) 
  }
  const increase = ()=>{
    reload.set(prev=>[...prev])
    cart.map(content=>{if((content.id === cartItem.id)){ content.quantity++; reload.set(prev=>[...prev])}})
    localStorage.setItem("cart",JSON.stringify(cart))
    
  }
  const decrease = ()=>{
    cart.map(
      content=>{if((content.id === cartItem.id)){ content.quantity-- ;reload.set(prev=>[...prev]);if(content.quantity<1){
        const newFilteredCart= cart.filter(newContent=>newContent.id !== cartItem.id)
        content.quantity=0 
      }}})
    localStorage.setItem("cart",JSON.stringify(cart))
  }
  
    return (
        <div className='checkout-item'>
          <div className='image-container'>
            <img src={Pepsi} alt='item' />
          </div>
          <span className='name'>{Name}</span>
          <span className='quantity'>
            <span className="cartModifier" onClick={decrease}>{"<"}</span>
            {quantity}
            <span className="cartModifier" onClick={increase}>{">"}</span>
          </span>
          <span className='price'>N{Price}</span>
          <span className='remove'onClick={removeItem}>Remove</span>
        </div>
    )
}
